.nav-full{
    background-color: #001427;
    border: black solid 1px;
    height: 100vh;
}

.nav-header{
    border-bottom: black solid 3px;
    height: 170px;
}

.nav-header p{
    font-family: 'Bungee', cursive;
    color:#F4D58D;
    font-size: 32px;
    padding: 10px;
}

.post-button{
    background-color: white;
    width: 100%;
    height: 60px;
}

.accordion-button{
    background-color: #F4D58D;
    font-weight: 700;
}

.accordion-button:not(.collapsed){
    background-color: #F4D58D !important;
    color: #001427 !important; 
}
.bulletin {
    background-image: url(/bulletin-board/static/media/cork-board.8752bc7c.jpg);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.post-full {
    background-color: #F4D58D;
    border: black solid 2px;
    position: absolute;
    width: 600px;
    padding: 20px;
}
.post-list {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.post-complete {
    background-color: #F4D58D;
    border: black solid 2px;
    width: 400px;
    padding: 20px;
    margin: 40px;
}
.post-title{
    border: 2px black solid;
    text-align: center;
    background-color: #001427;
    color: #F4D58D;
    font-family: 'Bungee', cursive;
    padding: 15px 0px 10px 0px;
}
.post-inputs{
    border: black solid 1px;
    border-radius: 10px;
    padding: 20px;
    margin-top: 20px;
}
.buttons{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 20px;
}
.time-available{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
#time-available{
    width: 80%;
}
.google-map{
    width: 100%;
}
